.home{
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
}

.home__row{
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    z-index: 1;
}


.home__img{
    width: 100%;
    mask-image: linear-gradient(to bottom,rgba(0, 0, 0, 1),rgba(0, 0, 0, 0));
    z-index: -1;
    margin-bottom: -150px;
}