.login__logo{
    width: 100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
}
.login{
   background-color: white; 
   height: 100vh;
   display: flex;
   flex-direction: column;
   align-items: center;
}
.login__container{
    display: flex;
    flex-direction: column;
    width:300px;
    padding:20px ;
    border: 1px solid lightgrey;

}
.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container > form > h5{
    margin-bottom: 5px;
}
.login__container > form > input{
    height: 30px;
    margin-bottom: 10px;
    width: 98%;
    background-color: white;
}
.login__container > p{
    margin-top: 15px;
    font-size: 12px;

}
.login__signin{
background-color: #f0c14b;
border-radius: 2px;
width: 100%;
height: 30px;
border: 1px solid;
margin-top: 10px;
border-color: #a88734 #9c7e31 #846a29;


}

.login__register{
   
border-radius: 2px;
width: 100%;
height: 30px;
border: 1px solid;
margin-top: 10px;
border-color:darkgrey;
}