.product{
    display: flex;
    align-items: center;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    align-items: center ;
    margin: 10px;
    padding: 20px;
    width:100%;
    justify-content: flex-end;
    background-color: white;
    z-index: 1;
}
.product__info{
    height: 100px;
    margin-bottom: 15px;
    
}



.product__rating{
    display: flex;
   
}
.product > img{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
   
   margin-bottom: 15px;
}
.product__price{
    margin-top: 5px;
    
}
.product > button{
    background-color: #f0c14b;
    border: 1px solid;
    
    border-color: #a88734 #9c7e31 #846a29;
   
}